@keyframes pop {
  0% { transform: scale(0.8); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1);}
}

@keyframes jump {
  0% { top: 0px; background-color: var(--color-correct);}
  50% { top: -20px; background-color: var(--color-correct);}
  100% { top: 0px; background-color: var(--color-correct);}
}

@keyframes shake {
  0%  { left: 0px; }
  10% { left: 5px; }
  20% { left: 0px; }
  30% { left: -5px; }
  40% { left: 0px; }
  50% { left: 5px; }
  60% { left: 0px; }
  70% { left: -5px; }
  80% { left: 0px; }
  90% { left: 5px; }
  100% { left: 0px; }
}

@keyframes flip-absent {
  0% {
    transform: rotateX(0);
    background-color: rgba(0, 0, 0, 0);
    color: var(--color-tone-1);
    border: 2px solid var(--color-tone-2);
  }
  49% {
    transform: rotateX(-90deg);
    background-color: rgba(0, 0, 0, 0);
    color: var(--color-tone-1);
    border: 2px solid var(--color-tone-2);
  }
  50% {
    transform: rotateX(-90deg);
    background-color: rgba(0, 0, 0, 0);
    color: white;
    border: none;
  }
  100% {
    transform: rotateX(0);
    background-color: var(--color-absent);
    color: white;
    border: none;
  }
}

