.game-tile-container {
  margin: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: xx-large;
  font-weight: bold;
  position: relative;
}

.game-tile {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  box-sizing: border-box;
  transform: rotateX(0);
  animation-fill-mode: forwards;
  animation-duration: 250ms;
}

@media only screen and (max-width: 500px) {
  .game-tile-container {
    font-size: x-large;
  }
}